html {
  scroll-behavior: smooth;
  writing-mode: horizontal-tb;
  direction: rtl;
}

:root {
  --primary-color: #5A5A5A;
  --black-color: #000000;
  --white-color: #ffffff;
  --grey-color: #5A5A5A;
  --grey-color-light: #ccceef;
  --blue-color: #2E3191;
  --yellow-color: #FFCA1D;
  --purple-color: #7670B3;
  --green-color: #92C446;
  --red-color: #931E5A;
  --red-color-light: #f1c7dc;
  --el-messeri: "El Messiri", sans-serif;
  --basefont: "Avenir LT Std";
}

*,
::after,
::before {
  box-sizing: border-box;
}
*:focus,
::after:focus,
::before:focus {
  outline: none;
}

body {
  font-family: "Avenir LT Std";
  font-size: 16px;
  line-height: 1.14;
  font-weight: 400;
  color: #5A5A5A;
  text-align: right;
  overflow-x: hidden;
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  direction: rtl;
}
@media (max-width: 1400px) and (min-width: 1300px) and (min-height: 900px) {
  body {
    font-size: 11.485px;
  }
}
@media (max-width:1199px) {
  body {
    font-size: 12px;
    overflow-x: hidden;
  }
}
@media (max-width:767px) {
  body {
    font-size: 14px;
  }
}
body.disabled-scroll {
  position: fixed;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
body.disabled-scroll::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  width: 100vh;
  height: 100vh;
  z-index: 6;
  animation: overlay 0.7s ease 0s 1 forwards;
}

@keyframes overlay {
  from {
    right: 100%;
  }
  to {
    right: 0;
  }
}
.loadjs {
  overflow: hidden;
}

