.thm-btn {
  font-family: "Avenir LT Std";
  font-size: 1.125em;
  font-weight: 700;
  line-height: 1.3125em;
  color: #ffffff;
  background-color: transparent;
  text-transform: capitalize;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  border-radius: 1.875em;
  padding: 0;
  border: 2px solid #931E5A;
  width: 10.722em;
  display: flex;
  align-items: center;
  position: relative;
}
@media (max-width:575px) {
  .thm-btn {
    width: 10em;
  }
}
.thm-btn__text {
  background: #931E5A;
  border: 1px solid #931E5A;
  border-radius: inherit;
  padding: 0.5em 0;
  flex: 0 1 70%;
  transition: all 0.5s ease-in-out;
  box-shadow: 0 0 0 1px #931E5A;
}
@media (max-width:575px) {
  .thm-btn__text {
    padding: 0.5em 0;
  }
}
.thm-btn__icon {
  position: absolute;
  z-index: 1;
  left: 7%;
  top: 52%;
  transform: translateY(-50%);
  transition: all 0.5s ease-in-out;
}
.thm-btn__icon svg {
  width: 1.625em;
}
@media (max-width:575px) {
  .thm-btn__icon svg {
    width: 1.4em;
  }
}
.thm-btn__icon path {
  transition: all 0.5s ease-in-out;
}
.thm-btn:hover .thm-btn__text {
  flex-grow: 1;
}
.thm-btn:hover .thm-btn__icon {
  left: 9%;
}
@media (max-width:575px) {
  .thm-btn:hover .thm-btn__icon {
    left: 7%;
  }
}
.thm-btn:hover .thm-btn__icon path {
  fill: #ffffff;
}
.thm-btn--blue {
  border-color: #2E3191;
}
.thm-btn--blue .thm-btn__text {
  background-color: #2E3191;
  border-color: #2E3191;
  box-shadow: 0 0 0 1px #2E3191;
}
.thm-btn--blue .thm-btn__icon path {
  fill: #2E3191;
}
.thm-btn--gray {
  border-color: #5A5A5A;
}
.thm-btn--gray .thm-btn__text {
  background-color: #5A5A5A;
  border-color: #5A5A5A;
  box-shadow: 0 0 0 1px #5A5A5A;
}
.thm-btn--gray .thm-btn__icon path {
  fill: #5A5A5A;
}
.thm-btn--white {
  border-color: #ffffff;
}
.thm-btn--white .thm-btn__text {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #931E5A;
  box-shadow: 0 0 0 1px #ffffff;
}
.thm-btn--white .thm-btn__icon path {
  fill: #ffffff;
}
.thm-btn--white:hover .thm-btn__icon path {
  fill: #931E5A;
}
.thm-btn--white-border {
  border-color: #ffffff;
}
.thm-btn--white-border .thm-btn__text {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #92C446;
  box-shadow: 0 0 0 1px #ffffff;
}
.thm-btn--white-border .thm-btn__icon path {
  fill: #ffffff;
}
.thm-btn--white-border:hover .thm-btn__icon path {
  fill: #92C446;
}
.thm-btn--transparant {
  color: #931E5A;
  text-align: right;
  border-color: transparent;
  width: 9.063em;
}
.thm-btn--transparant .thm-btn__text {
  background-color: transparent;
  border-color: transparent;
  color: #931E5A;
  box-shadow: 0 0 0 1px transparent;
}
.thm-btn--transparant:hover .thm-btn__icon path {
  fill: #931E5A;
}
.thm-btn--transparant:hover .thm-btn__text {
  color: #2E3191;
}

