.footer {
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    ul {
        margin: 0;
        list-style: none;
    }
    &__container {
        max-width: 100%;
        padding: 0 25px;
        @media (min-width: 1200px) {
            padding: 0 10.25%;
        }
        @media #{$mob} {
            padding: 0 17px;
        }
    }
    &__top {
        background: $blue-color;
        padding: em(65) 0 em(20);
        @media #{$ipad} {
            padding: 3em 0 2em;
        }
        @media #{$mob} {
            padding-top: 1em;
        }
        &--qlinks-wrapper {
            width: 100%;
            display: flex;
            justify-content: space-between;
            @media #{$ipad} {
                display: block;
            }
            .qlinks__col__wrapper {
                @media #{$ipad} {
                    margin-bottom: 1em;
                }
            }
        }

    }
}

.footer {
    &__top {
        &--qlinks-col {
            & {
                display: flex;
                flex-direction: column;
                width: 25%;
                @media #{$ipad} {
                    width: 100%;
                }
            }

            p {
                font-size: em(22);
                font-weight: 400;
                color: var(--white-color);
                position: relative;
                display: block;
                margin-bottom: 2.5em;
                text-transform: capitalize;
                @media #{$ipad} {
                    display: inline-block;
                }
                &::after {
                    content: '';
                    position: absolute;
                    width: 13.136em;
                    height: 1px;
                    background: $white-color;
                    bottom: -0.5em;
                    opacity: 0.5;
                    left: 0;
                    @media #{$ipadpro} {
                        width: 11.136em;
                    }
                }
                @media #{$ipad} {
                    &::before,
                    &::after {
                        position: absolute;
                        background-color: $white-color;
                        left: 0;
                        top: 50%;
                        width: 2px;
                        height: 1em;
                        transition: all 0.5s ease-in-out;
                    }
                    &::before {
                        top: 0.05em;
                        left: 0.45em;
                    }
                    &:after {
                        left: 0;
                        width: 1em;
                        height: 2px;
                        transform: translate(0, -50%);
                    }
                }
            }

            @media #{$ipadpro} {
                padding: 0 0.75em;
            }
            @media #{$ipad} {
                padding: 0;
                p {
                    line-height: 1;
                    padding-left: 2em;
                    margin-bottom: 0;
                    &::before,
                    &::after {
                        content: '';
                        opacity: 1;
                    }
                    &.opened {
                        &::before {
                            transform: rotate(90deg);
                        }
                        &::after {
                            transform: rotate(90deg);
                            opacity: 0;
                        }
                    }
                }
            }
            &:nth-child(1) {
                p {
                    color: $white-color;
                    font-size: 1.125em;
                    max-width: 75%;
                    &.head {
                        margin-bottom: 1em;
                    }
                    @media #{$ipad} {
                        padding: 0;
                        max-width: 100%;
                        text-align: center;
                        margin-bottom: 1em;
                    }
                    @media #{$tab} {
                        max-width: 100%;
                        margin: 0 0 1em 0;
                    }
                    &::before,
                    &::after {
                        width: 0;
                        height: 0;
                    }
                }
            }
            &:last-child {
                p {
                    &::after {
                        width: 79%;
                        @media #{$ipad} {
                            width: 1em;
                        }
                    }
                }
            }
            .brand-logo {
                position: relative;
                margin: 0;
                a {
                    display: inline-block;
                    width: 11.875em;
                    height: auto;
                    margin-bottom: 2.5em;
                    @media #{$ipad} {
                        display: block;
                        margin: 2em auto;
                    }
                    
                }
            }
            .social {
                display: flex;
                flex-direction: column;
                width: em(188);
                align-items: center;
                @media #{$ipad} {
                    margin: 0 auto;
                    margin-bottom: 3em;
                }
            }
            .social__links {
                display: flex;
                flex-wrap: wrap;
                padding: 0;
                @media #{$ipad} {
                    margin: 0 0 1em 0;
                    display: flex;
                    justify-content: center;
                }
                li {
                    padding: 0 0.7em;
                    &:first-child {
                        padding: 0 0.7em 0 0;
                    }
                }
                a {
                    line-height: 1;
                    display: block;
                    width: 1.125em;
                    height: 1.125em;
                    svg,
                    path {
                        width: 100%;
                        height: 100%;
                        fill: $primary-color;
                        transition: 0.3s;
                    }
                    &:hover {
                        svg,
                        path {
                            fill: $white-color;
                            transition: 0.3s;
                        }
                    }
                }
            }

            &--logo-panel {
                .brand-logo {
                    img {
                        margin: 0 auto;
                    }
                }   
                .cta__social__icons {
                    margin: 0;
                    justify-content: flex-start;
                    &__single {
                        border-color: $white-color;
                        svg {
                            path {
                                fill: $white-color;
                            }
                        }
                    }
                }
            }

            &:not(.footer__top--qlinks-col--logo-panel) {
                a {
                    font-size: em(22);
                    color: $white-color;
                    font-weight: 600;
                    font-family: var(--el-messeri);
                    text-decoration: none;
                    transition: $transition-ease;
                    display: flex;
                    align-items: center;
                    &:hover {
                        color: $yellow-color;
                    }
                }
            }

            &:last-child {
                .qlinks-menu {
                    &__item {
                        display: flex;
                        flex-wrap: wrap;
                        position: relative;
                        border-bottom: 1px solid #ffffff87;
                        max-width: 79%;
                        padding-bottom: 1em;
                        @media #{$mob} {
                            margin-bottom: 1.5em;
                        }
                        span {
                            font-size: em(22);
                            color: $white-color;
                            font-weight: 600;
                            font-family: var(--el-messeri);
                            text-decoration: none;
                            transition: all .3s ease-in-out;
                            width: 100%;
                            margin-bottom: 0;
                            display: flex;
                            align-items: center;
                            justify-content: space-between;
                            flex: 0 1 100%;
                            &:hover {
                                color: $yellow-color;
                            }
                        }
                        .icon {
                            width: 12px;
                            margin-right: 12px;
                        }
                        a {
                            flex: 0 1 73%;
                            text-transform: uppercase;
                            @media #{$mob} {
                                flex-basis: 91%;
                            }
                        }
                        & {
                            // &::before {
                            //     content: "";
                            //     position: absolute;
                            //     width: 79%;
                            //     height: 1px;
                            //     background: $white-color;
                            //     top: em(-12);
                            //     opacity: .5;
                            //     left: 0;
                            //     @media #{$mob} {
                            //         // top: -5px;
                            //     }
                            // }
                        }
                    }
                }
            }

        }
    }
}

.qlinks-menu {
    padding: 0;
    @media #{$ipad} {
        padding: 1.5em 0 1em 2em;
        display: none;
    }
    &__item {
        font-size: 1em;
        font-weight: 400;
        padding: 0 1em 0 0;
        margin-bottom: 1.5em;
        @media #{$ipad} {
            margin-bottom: 1.5em;
        }
        @media #{$mob} {
            margin-bottom: 0.5em;
        }

        .uline {
            position: relative;
            &::after {
                content: "";
                position: relative;
                background: url('../svg/white-right-long-arrow.svg') no-repeat;
                width: 20px;
                height: 12px;
                left: 0.7em;
                opacity: 0;
                transition: $transition-ease;
                display: block;
            }
            &:hover::after {
                opacity: 1;
                left: 0.5em;
            }
        }
        
        .days {
            display: block;
        }
    }

    & &__accordion {
        &.hide {
            display: none;
        }
        &.show {
            display: block;
        }
        &__wrapper {
            display: flex;
            margin-top: 1em;
        }
    }
    &__acc__title {
        position: relative;
        flex: 0 0 37%;
        cursor: pointer;
        @media #{$mob} {
            flex-basis: 40%;
        }
        &::after {
            content: "";
            position: relative;
            background: url('../svg/chevron-down.svg') center center/contain no-repeat;
            width: 16px;
            height: 16px;
            right: 0;
            display: block;
            flex: 0 0 16px;
        }
    }
}



.footer__bottom {
    position: relative;
    padding: 1.5em 0;
    border-top: 1px solid rgba($grey-color-light, 0.2);
    background: var(--purple-color);
    @media #{$tab} {
        padding: 1.5em 1em .5em 1em;
    }
}

.bottom-wrap {
    display: flex;
    justify-content: space-between;
    p {
        color: $white-color;
        font-size: 1em;
        font-weight: 400;
        margin: 0;
        &.copy {
            a {
                color: $white-color;
                transition: $transition-ease;
                &:after {
                    width: 100%;
                }
                &:hover {
                    color: $yellow-color;
                    &:after {
                        width: 0;
                    }
                }
            }
            span {
                color: $primary-color;
            }
            .vline {
                color: $grey-color-light;
                margin: 0 1em;
            }
        }
    }
    @media #{$ipad} {
        flex-flow: column;
        .copy {
            margin: 0 0 1em;
            text-align: center;
        }
        .social {
            justify-content: center;
            margin-bottom: 1em;
        }
    }
    .copy {
        &:last-child {
            flex: 0 1 28%;
            display: flex;
            justify-content: space-between;
            @media #{$ipad} {
                flex-basis: auto;
                justify-content: center;
            }
        }
    }
}


.cn-position-bottom {
    .cn-button {
        background-color: #f3931f !important; 
    }
    .cn-close-icon {
        opacity: 1;
        @media #{$ipadpro} {
            right: 0;
            top: 0;
            margin-top: 10px;
        }
        &::before,
        &::after {
            background-color: $white-color;
        }
    }
}